.cart {
    display: flex;
    padding: 20px;
    background-color: white;
    min-height:80vh; 
    justify-content:space-evenly;
    /* padding-top:3%; */
    /* height: max-content; */
}

.cart_title {
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
}

.cart_left {
    display: flex;
    flex-direction: column;
}


.subtotal {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    height: 100px;
    padding: 20px;
    background-color: #f3f3f3;
    border: 1px solid #dddddd;
    border-radius: 3px;
}

.subtotal_gift {
    display: flex;
    align-items: center;
}

.subtotal_gift > input {
    margin-right: 5px;
}

